.grid {
    display: grid;
    /* grid-template-columns: repeat(2, 1fr); */
    gap: 20px;
    padding: 20px;
    /* width: 50%;        */
    box-sizing: border-box;  /* Includes padding in the width calculation */
    margin-left: auto;      
    margin-right: auto;      
}

/* For medium screens */
@media (min-width: 600px) {
    .grid {
        grid-template-columns: repeat(2, 1fr); /* 2 columns for medium screens */
        width: 80%; /* Reduce width to 80% for medium screens */
    }
}

/* For large screens */
@media (min-width: 900px) {
    .grid {
        grid-template-columns: repeat(2, 1fr); /* 3 columns for large screens */
        width: 100%; /* Reduce width to 60% for large screens */
    }
}


.grid-item {
    background-color: transparent; /* Change this line */
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    transition: 0.5s;
}
.grid-item::before {
    content: "";
    display: block;
    padding-top: 100%; /* This creates the square shape */
}

.grid-item:hover {
    transform: scale(1.2); /* Enlarge the grid box */
    z-index: 1; /* Add this line to bring the item to the front when hovered */

}

@media (min-width: 600px) {
    .grid-item {
        padding: 20px; /* Increase padding for medium screens */
    }
}

/* For large screens */
@media (min-width: 900px) {
    .grid-item {
        padding: 30px; /* Increase padding for large screens */
    }
}
.grid-item.item2 {
    background-image: url('../assets/My\ first\ design.png');
    background-position: 0% 30%; /* Center the background image */
    background-size: 100%; /* Decrease the size of the background image */
    background-repeat: no-repeat; /* Stop the background image from repeating */

}

